.nav-btn {
  position: absolute;
  top: 12px;
  right: 15px;
  z-index: 3;
  width: 20px;
  height: 20px;
  display: flex;
}

.nav-btn span {
  width: 30px;
  height: 5px;
  background: #fff;
  display: flex;
  position: absolute;
  transition: 0.3s;
  border-radius: 2.5px;
}

.nav-btn span:nth-child(1) {
  top: 20%;
}

.nav-btn span:nth-child(2) {
  top: 80%;
}

.nav-btn.is-active span {
  background: #fff;
}

.nav-btn.is-active span:nth-child(1) {
  top: 50%;
  transform: rotate(-45deg);
}

.nav-btn.is-active span:nth-child(2) {
  top: 50%;
  transform: rotate(45deg);
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: #000;
  padding: 100px;
  z-index: 2;
  transition: 0.3s;
  flex-direction: column;
  visibility: hidden;
  opacity: 0;
}

.nav-menu.is-active {
  visibility: visible;
  opacity: 1;
}

.nav-list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
}

.nav-link {
  position: relative;
  display: inline-flex;
  font-weight: bold;
  font-size: 35px;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.nav-link:hover {
  color: #000;
}

.nav-link:after {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 0;
  height: 40px;
  display: inline-flex;
  background: #fff;
  width: 0;
  transition: 0.3s;
  z-index: -1;
}

.nav-link:hover:after {
  width: 105%;
  border-radius: 0 5px 5px 0;
}

.header {
  width: 100%;
  height: 100%;
  margin: 0;
  z-index: 1;
}

.header > img {
  width: 100%;
  object-fit: cover;
}

.content1 {
  border-radius: 15px 15px 0 0;
  margin-top: -1em;
  position: absolute;
  z-index: 1;
  background-color: #ffffff;
  padding: 20px;
}

* {
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
  color: #000;
  margin: 0;
  padding: 0;
}

.background {
  background-size: cover;
